import React from 'react';
import {Chart} from 'primereact/chart';
import lineChart_data from './lineChart_data';
import '../App.css';

export default class LineChart extends React.Component {
    render() {
        return (
            <div className="data3">
                <Chart type="line" data={lineChart_data} />
            </div>
        );
    }
}