import React from 'react';
import {DataTable} from 'primereact/datatable';
import {Column} from 'primereact/column';
import {Dropdown} from 'primereact/dropdown';
import table from  './table';
import table2 from  './table2';
import table3 from  './table3';
import table4 from  './table4';
import '../App.css';

export default class TableData extends React.Component {
    constructor() {
        super();
        this.state = {
            jsonData: table.data,
            jsonData2: table2.data,
            jsonData3: table3.data,
            jsonData4: table4.data,
            city: null
        };
        this.onYearChange = this.onYearChange.bind(this);
    }

    onYearChange(e) {
        this.setState({city: e.value.value});
    }

    render() {
        const cities = [
            {label: '2017', value: this.state.jsonData},
            {label: '2018', value: this.state.jsonData2},
            {label: '2019', value: this.state.jsonData3},
            {label: '2020', value: this.state.jsonData4}
        ];

        return (
            <div className="container">
                <div className="data1">
                    <Dropdown value={cities.label} options={cities} placeholder="Select the year" optionLabel="label" onChange={this.onYearChange}/>
                    <div>
                        {this.state.city ? 
                        <DataTable value={this.state.city} style={{width: '350px'}} paginator={true} rows={10} rowsPerPageOptions={[5,10,20]} sortMode="multiple">                        
                            <Column field="vin" header="Client Name" sortable={true} />
                            <Column field="brand" header="Notional Amount" sortable={true}/>
                        </DataTable>  : 
                        <strong>Please select a year</strong>}
                    </div>
                </div>
            </div>
        );
    }
}