import React from 'react';
import {Chart} from 'primereact/chart';
import chartData from './chartData';
import '../App.css';

export default class DonutChart extends React.Component {
    
    render() {
            return (
                <div className="data2">
                    <Chart type="doughnut" data={chartData} />
                </div>
            );
    }
}